

import Vue from 'vue'
import router from './router'
import store from './store'
import { sync } from 'vuex-router-sync'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import 'styles/index.scss';
import App from './App'
import 'lib-flexible'
import VueClipboards from 'vue-clipboards';

global.keythereum = require('keythereum');
var Web3 = require('web3');

Vue.use(VueRouter)
Vue.use(VueClipboards)

Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)
Vue.use(LoadingPlugin)

sync(store, router)

if (typeof web3 !== 'undefined') {
global.web3 = new Web3(web3.currentProvider);
} else {

global.web3 = new Web3(new Web3.providers.HttpProvider("https://ropsten.infura.io/fAwfCBb0kqGbPPGyhhz8"));
}
FastClick.attach(document.body)

Vue.config.productionTip = false

store.registerModule('vux', {
state: {
isLoading: false
},
mutations: {
updateLoadingStatus(state, payload) {
state.isLoading = payload.isLoading
}
}
})
router.beforeEach((to, from, next) => {
next();
store.commit('updateLoadingStatus', { isLoading: true })
})
router.afterEach(function (to) {
setTimeout(function () {
store.commit('updateLoadingStatus', { isLoading: false })
}, 500)
})



























/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



